@keyframes hand-move {
  from {
    transform: rotate(90deg);
  }

  to {
    transform: rotate(450deg);
  }
}

.stop-watch {
  border: 7px solid #000;
  border-radius: 50%;
  width: 30rem;
  height: 30rem;
  margin-top: 50px;
  padding: 2rem;
  position: relative;
}

.stop-watch-outer-face {
  border-radius: 100%;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.stop-watch-outer-face:after {
  transform: rotate(90deg);
}

.stop-watch-outer-face:after, .stop-watch-outer-face:before, .stop-watch-outer-face-markings {
  content: "";
  z-index: 0;
  background: orange;
  width: 5px;
  height: 100%;
  position: absolute;
  left: 49%;
}

.stop-watch-outer-face-markings {
  background: #000;
  width: 3px;
}

.stop-watch-outer-face-markings--one {
  transform: rotate(30deg);
}

.stop-watch-outer-face-markings--two {
  transform: rotate(60deg);
}

.stop-watch-outer-face-markings--three {
  transform: rotate(120deg);
}

.stop-watch-outer-face-markings--four {
  transform: rotate(150deg);
}

.stop-watch-inner-face {
  z-index: 1;
  background: #f8f8ff;
  border-radius: 100%;
  width: 80%;
  height: 80%;
  position: absolute;
  top: 10%;
  left: 10%;
}

.stop-watch-inner-face:before {
  content: "";
  z-index: 11;
  background: #4d4b63;
  border-radius: 18px;
  width: 16px;
  height: 16px;
  margin-top: -6px;
  margin-left: -9px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stop-watch-inner-face-hand {
  transform-origin: 100%;
  background: #000;
  border-radius: 6px;
  width: 50%;
  height: 6px;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: rotate(90deg);
}

.stop-watch-inner-face-hand--minute {
  z-index: 10;
  width: 40%;
  height: 3px;
  animation: 60s linear infinite forwards hand-move;
}

.stop-watch-inner-face-hand--second {
  background: orange;
  width: 45%;
  height: 2px;
  animation: 5s linear infinite forwards hand-move;
}

body {
  background-color: #f8f8ff;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

